@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
@import url("https://fonts.googleapis.com/css?family=Slabo+27px");
.main-index .items-content .user-item {
  border: 1px solid #e9e9e9;
  background-color: #fff;
  -webkit-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
  transition: all .25s;
}

.main-index .items-content .user-item:hover {
  -webkit-box-shadow: 0px 0px 30px -7px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 0px 30px -7px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 30px -7px rgba(0, 0, 0, 0.5);
}

.main-index .items-content .user-item .user-thumbnail {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  font-size: 18px;
  line-height: 70px;
  text-align: center;
  margin: 0 auto;
  margin-top: 20px;
  cursor: default;
}

.main-index .items-content .user-item .user-name {
  margin: 20px 0px;
  text-align: center;
}

.main-index .items-content .user-item .user-name .name {
  margin-bottom: 8px;
  color: #1f2437;
  font-weight: 300;
}

.main-index .items-content .user-item .user-name .username {
  margin: 0px;
  color: #b2b3b7;
}

.main-index .items-content .user-item .tasksHolder {
  text-align: center;
  border-top: 1px solid #e9e9e9;
  border-bottom: 1px solid #e9e9e9;
  padding: 10px 0px;
  cursor: default;
}

.main-index .items-content .user-item .tasksHolder .taskCount {
  margin: 0px;
  font-size: 28px;
  font-weight: 300;
  color: #1f2437;
}

.main-index .items-content .user-item .tasksHolder .description {
  color: #b2b3b7;
  font-size: 12px;
  font-weight: 300;
  margin: 0px;
}

.main-index .items-content .user-item .linksHolder {
  padding: 0px;
}

.main-index .items-content .user-item .linksHolder a {
  padding: 15px 0px;
  text-align: center;
  font-size: 10px;
  text-decoration: none;
  color: #b2b3b7;
  letter-spacing: 1px;
  transition: color .25s ease-in-out;
}

.main-index .items-content .user-item .linksHolder a:hover {
  color: #4687ff;
}

.main-index .items-content .user-item .linksHolder a:first-child {
  border-right: 1px solid #e9e9e9;
}

.main-index .items-content .user-item .linksHolder a .icon {
  font-size: 15px;
  margin-right: 5px;
  position: relative;
  top: 2px;
}

/* Small Devices, Tablets */
@media only screen and (max-width: 767px) and (min-width: 480px) {
  .main-index .items-content .user-item:hover {
    -webkit-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
    box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) and (min-width: 320px) {
  .main-index .items-content .user-item:hover {
    -webkit-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
    box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
  }
}

/* Custom, iPhone Retina */
@media only screen and (max-width: 320px) {
  .main-index .items-content .user-item:hover {
    -webkit-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
    box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
  }
}
